<template>
  <div class="member-order-detail" v-if="order">
    <!-- 头部 -->
    <DetailAction :order="order" />
    <!-- 进度 -->
    <DetailStep :order="order" />
    <!-- 物流  Suspense异步组件-->
    <!-- <Suspense>
      <template #default>
        <DetailLogistics
          v-if="[3, 4, 5].includes(order.orderState)"
          :order="order"
        />
      </template>
      <template #fallback>
        <div class="loading">loading</div>
      </template>
    </Suspense> -->
    <Suspense v-if="[3, 4, 5].includes(order.orderState)">
      <!-- 组件加载完毕 -->
      <template #default>
        <DetailLogistics :order="order" />
      </template>
      <!-- 组件加载中显示 -->
      <template #fallback>
        <div class="loading">loading</div>
      </template>
    </Suspense>
    <!-- 信息 -->
    <DetailInfo :order="order" />
  </div>
</template>

<script>
import DetailInfo from './components/detail-info.vue'
import DetailLogistics from './components/detail-logistics.vue'
import { findOrderDetail } from '@/api/order'
import DetailStep from './components/detailStep.vue'
import DetailAction from './components/detail-action.vue'
import { useRoute } from 'vue-router'
import { ref } from 'vue'
export default {
  name: 'MemberDetail',
  components: { DetailAction, DetailStep, DetailLogistics, DetailInfo },
  setup () {
    const route = useRoute()
    // 订单
    const order = ref(null)
    findOrderDetail(route.params.id).then(data => {
      order.value = data.result
    })
    return {
      order
    }
  }
}
</script>

<style lang="less" scoped>
.member-order-detail {
  background-color: #fff;
  height: 100%;
}
.loading {
  height: 50px;
  display: flex;
  align-items: center;
  padding: 0 30px;
  background-color: #f5f5f5;
  margin: 30px 50px 0;
}
</style>
